
.dialog-outter-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  pointer-events: none;
}
.dialog-mask {
  opacity: 0.7;
  background: rgb(0, 0, 0);
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: -1;
}
.dialog-wrapper {
  min-width: 120px;
  position: absolute;
  background-color: #fff;
  border: 1px solid #999;
  border-radius: 6px;
  outline: 0;
  top: 50%;
  left: 50%;
  -webkit-transition: all .15s ease-in-out;
  transition: all .15s ease-in-out;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  z-index: 1025;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  pointer-events: all;
}
.dialog-close {
  position: absolute;
  right: 6px;
  top: 6px;
  cursor: pointer;
  text-decoration: none;
  outline: 0;
  padding: 0 4px;
  font-size: 21px;
  font-weight: 700;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: .2;
}
.dialog-close:hover {
  opacity: .5;
}
.dialog-title {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 700;
  cursor: default;
  text-align: center;
  border-bottom: 1px solid #e5e5e5;
  padding: 12px 0 16px 0;
}
.dialog-content {
  padding: 25px 20px 25px 20px;
}
.dialog-footer::after {
  display: block;
  clear: both;
  visibility: hidden;
  height: 0;
  overflow: hidden;
  content: "."
}
.dialog-buttons {
  float: right;
  font-size: 0;
  padding-bottom: 12px;
  text-align: center;
}
.dialog-statusbar {
  float: left;
  text-align: center;
  padding: 6px 12px;
  white-space: nowrap;
}
.dialog-statusbar input {
  vertical-align: middle;
}
.dialog-button {
  display: inline-block;
  text-align: center;
  padding: 6px 12px;
  white-space: nowrap;
  border: 1px solid transparent;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
  border-radius: 4px;
  color: #fff;
  background-color: #428bca;
  border-color: #357ebd;
  margin-left: 10px;
}
.dialog-button:last-child {
  margin-right: 10px;
}
.dialog-button:hover {
  background-color: #3276b1;
  border-color: #285e8e;
}
.button-confirm {
  color: #fff;
  background-color: #428bca;
  border-color: #357ebd;
}
.button-cancel {
  color: #333;
  background-color: #fff;
  border-color: #ccc;
}
.button-cancel:hover {
  color: #333;
  background-color: #ebebeb;
  border-color: #adadad;
}
.button-confirm:hover {
  background-color: #3276b1;
  border-color: #285e8e;
}
.dialog-anchor-arrow {
  position: absolute;
  width: 16px;
  height: 16px;
  overflow: hidden;
